<template>
  <!-- 首页 -->
  <div class="home-box">
    <p
      class="item-icon"
      v-for="item in navList"
      :key="item.name"
      @click="toLongText(item.pathName)"
    >
      <img :src="item.imgUrl" alt="" />
    </p>
  </div>
</template>

<script>
import { reactive, toRefs, onBeforeMount, onMounted } from 'vue';
import { useRouter } from 'vue-router';
import { navList } from '@/views/home.js';
export default {
  name: '',
  setup() {
    const data = reactive({
      navList
    });
    const router = useRouter();
    const toLongText = (name) => {
      router.push({
        name
      });
    };

    return {
      ...toRefs(data),
      toLongText
    };
  }
};
</script>
<style lang="less" scoped>
.home-box {
  width: 100%;
  height: 100%;
  padding: 20px 10px;
  .item-icon {
    width: 70px;
    float: left;
    margin: 0 10px;
    border-radius: 10px;
    cursor: pointer;
    transition: 0.3s;
    img {
      display: block;
      width: 100%;
      height: 70px;
    }
    &:hover {
      box-shadow: 0px 0px 10px #fff;
    }
  }
}
</style>
